import React, { useState } from "react";
import { useSelector } from "react-redux";
import Grid from "@material-ui/core/Grid";
import FoliaCard from "./FoliaCard";
import { MDBIcon } from "mdbreact";
import SwipeableViews from "react-swipeable-views";

export default function FoliaCarousel(props) {
  let produtos = useSelector(state => state.products.produtos);
  produtos = produtos.filter(produto => {
    return produto.type === "grouped";
  });
  produtos = produtos.filter(function(produto) {
    return produto.categories[0].id === props.categoriaID;
  });
  let NomeCategoria = "";
  if (produtos.length > 0) {
    NomeCategoria = produtos[0].categories[0].name;
  }

  const styles = {
    root: {
      padding: "0 30px"
    },
    slideContainer: {
      padding: "0 5px"
    },
    slide: {
      padding: 5,
      maxWidth: 300
    }
  };
  const [slideIndex, setSlideIndex] = useState(0);

  function slidePrevious() {
    let newindex = slideIndex - 1 >= 0 ? slideIndex - 1 : produtos.length - 1;
    setSlideIndex(newindex);
  }

  function slideNext() {
    let newindex = slideIndex + 1 < produtos.length ? slideIndex + 1 : 0;
    setSlideIndex(newindex);
  }

  return (
    <Grid container spacing={1}>
      <Grid
        item
        xs={6}
        className="h1-responsive font-weight-bold my-2"
        style={{ color: "#4A4A4A!important" }}
      >
        <h2 className="text-left ">{NomeCategoria}</h2>
      </Grid>
      <Grid item xs={6}>
        <h2 className="text-right">
          <MDBIcon className="mr-4" icon="angle-left" onClick={slidePrevious} />
          <MDBIcon icon="angle-right" onClick={slideNext} />
        </h2>
      </Grid>
      <Grid item xs={12}>
        <SwipeableViews
          style={styles.root}
          slideStyle={styles.slideContainer}
          index={slideIndex}
        >
          {produtos.map(item => (
            <div key={item.id} style={Object.assign({}, styles.slide)}>
              <FoliaCard dados={item} />
            </div>
          ))}
        </SwipeableViews>
      </Grid>
    </Grid>
  );
}
