import React from "react";
import { Skeleton } from "@material-ui/lab";
import { MDBCard, MDBBadge } from "mdbreact";
import { Link } from "react-router-dom";
import baseshape from "../../assets/base-shape.png";

function FoliaCard(props) {
  const produto = props.dados;
  const atributos = produto.attributes;
  const censura = atributos.find(obj => obj.name === "Censura");
  const imgurl = produto.images[0] ? produto.images[0].src : "";
  const checkImage = path =>
    new Promise(resolve => {
      const img = new Image();
      img.onload = () => resolve(path);
      img.onerror = () => false;

      img.src = path;
    });

  if (!checkImage) {
    return <Skeleton variant="rect" width={210} height={118} />;
  } else {
    return (
      <Link to="compre-aqui/">
        <MDBCard
          className="card-image thumb-bg"
          style={{
            backgroundImage: `url('${imgurl}')`,
            backgroundPosition: "center",
            backgroundSize: "contain"
          }}
        >
          <img
            src={baseshape}
            className="img-fluid base-shape"
            alt={produto.name}
          />

          <div className="text-white text-left d-flex flex-column p-4 position-absolute">
            <div className="text-left">
              <MDBBadge color="dark" className="p-1" title="censura">
                {censura ? censura.options[0] : ""}
              </MDBBadge>
            </div>
            <div className="text-left h4-responsive">{produto.name}</div>
            <div className="text-left">
              A partir de
              <b> R$ {produto.price}</b>
            </div>
          </div>
        </MDBCard>
      </Link>
    );
  }
}

export default React.memo(FoliaCard);
